<template>
  <div class="col-12 col-sm-10 col-md-12 col-lg-6" style="padding-left: 5px;padding-right: 5px">
    <div class="mb-5">
      <h5
          v-if="isDrink"
          style="
          color: #a07c37;
          font-family: Montserrat, sans-serif;
          font-weight: bold;
        "
      >
        {{ name }}
      </h5>
      <div v-if="isDrink" class="card" style="padding-bottom: 10px">
        <div
            class="card-img"
            style="margin-bottom: 10px;min-height: 400px!important;"

            v-bind:style="{backgroundImage: `url(${require('@/assets/img/'+fillBackground())})`}"></div>

        <div class="card-img-overlay d-flex justify-content-end"></div>
        <div
            class="card-body"
            style="padding-bottom: 0px; padding-top: 0px"
            v-for="subProduct in subProducts"
            :key="subProduct.id"
        >
          <div class="row">
            <div class="col col-9" style="text-align: start">
              <h4 class="card-title" style="margin:0!important;display: inline-block">{{ subProduct.name }}&nbsp;<span
                  style="
                    color: #a07c37;
                    font-size: 12px !important;
                    font-weight: bold;
                    white-space: nowrap;
                  "
                  v-html="subProduct.util"
              ></span></h4>
            </div>
            <div class="col col-3 col3" style="padding: 0">
              <div class="buy-d">
                <div class="price text-center">
                  <h5 class="">{{ subProduct.price }} ₼</h5>
                </div>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-10">
              <span style="opacity: 0.8">{{ subProduct.content }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
    this.fillBackground
  },
  data() {
    return {
      path: ""
    }
  },
  methods: {
    fillBackground() {
      try {
        require("@/assets/img/" + this.imgPath);
        return this.imgPath
      } catch (err) {
        return "default.jpg"
      }

    },
  },
  props: {
    readLessText:[String],
    readMoreText: [String],
    isDrink: [Boolean],
    id: [String],
    name: [String],
    price: [Number],
    imgPath: [String],
    content: [String],
    util: [String],
    subProducts: [Array],
  },

};
</script>