<template>
  <div class="col-12 col-sm-10 col-md-6 col-lg-6">
    <div class="mb-5">
      <div v-if="!isDrink" class="card meal-card">

        <div class="card-img back-image"
             v-bind:style="{backgroundImage: `url(${require('@/assets/img/'+fillBackground())})`}"></div>
        <div class="card-img-overlay d-flex justify-content-end"></div>
        <div class="card-body">
          <div class="row">
            <div class=" col-12">
              <h6 class="card-title">
                {{ name }}
                <span
                    style="
                    color: #a07c37;
                    font-size: 12px !important;
                    font-weight: bold;
                  "
                    v-html="util"
                ></span>
              </h6>
            </div>
          </div>
          <div class="row">
            <div class="col-10" style="">
              <read-more v-if="!$isMobile()&&content" class="read-more" style="display: inline"
                         :more-str="' '+readMoreText"
                         :less-str="' '+readLessText" :text="content" :max-chars="50" link="#"></read-more>
              <p v-else style="font-size: 14px">{{ content }}</p>
            </div>
            <div class="col-2" style="padding: 0!important;">
              <div class="buy">
                <div class="price text-center">
                  <p class="">{{ price }} ₼</p>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
      <!--      //Drinks-->
      <h5
          v-if="isDrink"
          style="
          color: #a07c37;
          font-family: Montserrat, sans-serif;
          font-weight: bold;
        "
      >
        {{ name }}
      </h5>
      <div v-if="isDrink" class="card" style="padding-bottom: 10px">
        <div
            class="card-img"
            style="margin-bottom: 10px"
            v-bind:style="{backgroundImage: `url(${require('@/assets/img/'+fillBackground())})`}"></div>

        <div class="card-img-overlay d-flex justify-content-end"></div>
        <div
            class="card-body"
            style="padding-bottom: 0px; padding-top: 0px"
            v-for="subProduct in subProducts"
            :key="subProduct.id"
        >
          <div class="row">
            <div class="col col-9">
              <h4 class="card-title" style="margin:0!important;">{{ subProduct.name }}&nbsp;<span
                  style="
                    color: #a07c37;
                    font-size: 12px !important;
                    font-weight: bold;
                  "
                  v-html="subProduct.util"
              ></span></h4>
            </div>
            <div class="col col-3 col3" style="padding: 0">
              <div class="buy-d">
                <div class="price text-center">
                  <h5 class="">{{ subProduct.price }} ₼</h5>
                </div>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-10">
              <span style="opacity: 0.8">{{ subProduct.content }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
    this.fillBackground
  },
  data() {
    return {
      path: ""
    }
  },
  methods: {
    fillBackground() {
      try {
        require("@/assets/img/" + this.imgPath);
        return this.imgPath
      } catch (err) {
        return "default.jpg"
      }

    },
  },
  props: {
    readLessText: [String],
    readMoreText: [String],
    isDrink: [Boolean],
    id: [String],
    name: [String],
    price: [Number],
    imgPath: [String],
    content: [String],
    util: [String],
    subProducts: [Array],
  },

};
</script>