<template>
  <div class="col justify-content-center" style="padding:0!important">
    <p class="d-flex" style="margin-bottom:10px!important">
      <a
          class="nav-link"
          data-toggle="collapse"
          :href="'#' + collapsId"
          role="button"
          aria-expanded="false"
          :aria-controls="collapsId"
          :name="collapsId"
          :id="index"
          @click="checkOpenStatus"
      >
        {{ name }}
      </a>
    </p>
  </div>
</template>
<script>
// import $ from "jquery"
export default {
  created() {

  },
  methods: {
    checkOpenStatus(event) {
      localStorage.setItem("lastCollapse",event.target.attributes.href.nodeValue.replaceAll("#",""))
      let name = event.target.name;
      let index = event.target.id
      let body = document.getElementById("body");
      let myContainer = body.childNodes[1];
      if (!this.$isMobile()) {
        if (myContainer.childNodes.length) {
          myContainer.childNodes.forEach(e => {
            e.getElementsByTagName("a")[0].style.borderLeft = '2px solid #232323';
          })

          let parentNeighbour = myContainer.childNodes[parseInt(index)+1]
          parentNeighbour.getElementsByTagName("a")[0].style.borderLeft = "none"
        }
      }
      let className = document.getElementById(name).classList;
      if (className.contains("show")) {
        console.log("yes this is open")
        event.preventDefault()
        event.stopPropagation();
      }

    }
  },
  props: {
    collapsId: [String],
    name: [String],
    index: [Number]
  },
  mounted() {
    let body = document.getElementById("body");
    let myContainer = body.childNodes[1];
    if (!this.$isMobile()) {
      if (myContainer.childNodes.length) {
        myContainer.childNodes.forEach(e => {
          e.getElementsByTagName("a")[0].style.borderLeft = '2px solid #232323';
        })
      }
    }
  }

}
</script>
