<script>
import axios from "axios";

export default {

  methods: {
    showPage() {
      document.getElementById("loader").style.display = "none";
      document.getElementById("app").style.display = "block";
    },
    hardRefresh(link) {
      this.lang = link;
      if (this.lang == 'az') {
        this.homePageText = "Ana Səhifə"
        this.contactText = "Əlaqə"
      } else if (this.lang == 'en') {
        this.homePageText = "Home Page"
        this.contactText = "Contact"
      } else {
        this.homePageText = "Главная"
        this.contactText = "Связаться с нами"
      }
    },
    submitForm() {
      let formData = new FormData();
      formData.append("fullname", this.fullname);
      formData.append("email", this.email);
      formData.append("phone", this.phone);
      formData.append("subject", this.title);
      formData.append("message", this.message);
      formData.append("where", "Rich Otel");
      console.log(formData);
      if (this.fullname && this.email && this.phone && this.title && this.message) {
        if (this.lang == 'en') {
          this.$swal("Survey completed");
        } else if (this.lang == 'az') {
          this.$swal("Sorğunuz tamamlanmışdır!");
        } else {
          this.$swal("Опрос завершен")
        }
        axios
            .post("http://localhost/mail/send", formData)
            .then((response) => console.log(response)
            );
      } else {
        if (this.lang == 'en') {
          this.$swal("Please fill in all fields.");
        } else if (this.lang == 'az') {
          this.$swal("Zəhmət olmasa bütün sahələri tamamlayın.");
        } else {
          this.$swal("Пожалуйста заполните все поля.")
        }
      }

    },

  },
  data() {
    return {
      lang: "az",
      fullname: null,
      title: null,
      email: null,
      phone: null,
      message: null,
      homePageText: "",
      contactText: ""
    };

  },

  created() {
    let language = this.$route.query.lang
    if (language == null) {
      this.lang = "az";
    } else {
      this.lang = language;
    }
    this.hardRefresh(language);
  },
  mounted() {
    window.addEventListener("load", () => {
      this.showPage();
    });
  }
};
</script>
