<template>

  <div class="super-parent">
    <nav
        class="navbar navbar-expand-lg navbar-dark"
        style="position: fixed; width: 100%; z-index: 3; min-height: 80px; top: 0;padding-left: 20px"
    >
      <div class="" style="width: 100%; display: flex">
        <div class="col-8" style="display: inline-grid">
          <a class="navbar-brand" @click="hardRefresh(lang)">
            <img src="@/assets/logo.png" :width="80"/></a>
        </div>
        <div class="col-2 center">
          <button type="button" class="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true"
                  aria-expanded="false">
            {{ lang.toUpperCase() }}
          </button>
          <div class="dropdown-menu">
            <a @click="hardRefresh('az')" class="dropdown-item lang-link">AZ</a>
            <a @click="hardRefresh('en')" class="dropdown-item lang-link">EN</a>
            <a @click="hardRefresh('ru')" class="dropdown-item lang-link">RU</a>
          </div>
        </div>
        <div class="col-2 center">
          <button
              class="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#navbarNavAltMarkup"
              aria-controls="navbarNavAltMarkup"
              aria-expanded="false"
              aria-label="Toggle navigation"
          >
            <span class="navbar-toggler-icon"></span>
          </button>
        </div>
      </div>

      <div class="collapse navbar-collapse" id="navbarNavAltMarkup" style="padding-top: 15px;">
        <div class="navbar-nav text-center">
          <router-link  class="nav-item nav-link active"
             data-toggle="collapse"
             data-target="#navbarNavAltMarkup"
             aria-controls="navbarNavAltMarkup"
             aria-expanded="false"
             aria-label="Toggle navigation"
             :to="'/'"
          > {{homePageText}}<span class="sr-only">(current)</span></router-link>
          <router-link class="nav-item nav-link active"
             data-toggle="collapse"
             data-target="#navbarNavAltMarkup"
             aria-controls="navbarNavAltMarkup"
             aria-expanded="false"
             aria-label="Toggle navigation"
             :to="'/contact?lang='+lang"
          >{{contactText}} <span class="sr-only">(current)</span></router-link>
        </div>
      </div>
    </nav>
<div class="col-12">
  <p style="margin-bottom: 0!important;margin-top: 80px;color: rgb(160, 124, 55);">10% service charge</p>
</div>
    <nav class="" style="margin-top:80px" id="body">
      <div class="container">
        <div class="col text-center" style="margin-bottom: 50px">
          <h1

              style="
         color: #a07c37;;
          font-family: 'Montserrat', sans-serif;
          font-weight: bold;
        "
          >
            {{ menuText }}
          </h1>

        </div>
      </div>

      <div class="row nav-tabs" style="justify-content: center; margin-bottom: 50px;margin-right: 0px;margin-left: 0px">
        <div v-for="(category,index) in mainCategory.categories" class="category"
             :key="category.id">
          <Category :collapsId="category.collapsId" :name="category.name" :index="index"/>
        </div>

        <div id="accordion" style="margin-top: 50px">
          <div
              v-for="category in mainCategory.categories"
              :key="category.id"
              class="collapse"
              :id="category.collapsId"
              data-parent="#accordion"
          >
            <h5
                style="
              color: #a07c37;
              font-family: Montserrat, sans-serif;
              font-weight: bold;
              margin-bottom:10px;
            "
                v-if="category.meals"
            >
              {{ category.name }} <span>
              <img class="category-logo" :src="require('@/assets/img/logos/'+category.logo)"/>
            </span>
            </h5>
            <div class="row" v-if="category.meals">
              <Product
                  v-for="meal in category.meals"
                  :key="meal.id"
                  :id="category.collapsId"
                  :price="meal.price"
                  :content="meal.content"
                  :imgPath="meal.imgPath"
                  :name="meal.name"
                  :util="meal.util"
                  :read-more-text="readMoreText"
                  :read-less-text="readLessText"
                  :is-drink="false"
              />
            </div>
            <!-- İçkilər üçün -->
            <div class="row" v-if="category.drinks">
              <ProductDrink
                  v-for="drink in category.drinks"
                  :key="drink.id"
                  :id="category.collapsId"
                  :price="drink.price"
                  :content="drink.content"
                  :imgPath="drink.logo"
                  :name="drink.name"
                  :util="drink.util"
                  :is-drink="true"
                  :sub-products="drink.subProducts"
              />
            </div>
          </div>
        </div>
      </div>
    </nav>
  </div>
</template>
<script>
import Product from "./Product.vue";
import ProductDrink from "@/components/ProductDrink";
import Category from "./Category.vue";
import categories from "./data.json"

export default {
  props: {},
  components: {
    ProductDrink,
    Product,
    Category,
  },
  created() {
    if (this.lang == 'az') {
      this.readMoreText = "daha çox"
      this.readLessText = 'daha az'
      this.menuText = 'Menyu'
      this.mainCategory = categories.categoriesAZ;
      this.homePageText="Ana Səhifə"
      this.contactText="Əlaqə"
    } else if (this.lang == 'en') {
      this.readMoreText = 'read more'
      this.readLessText = 'read less'
      this.menuText = "Menu"
      this.homePageText="Home Page"
      this.contactText="Contact"
      this.mainCategory = categories.categoriesEN
    } else {
      this.readMoreText = 'читать больше'
      this.readLessText = 'читать меньше'
      this.menuText = 'Меню'
      this.homePageText="Главная"
      this.contactText="Связаться с нами"
      this.mainCategory = categories.categoriesRU;
    }
    // let language = this.$route.query.lang
    // if (language == null) {
    //   this.lang = "az";
    // } else {
    //   this.lang = language;
    // }
  },
  methods: {
    prepareCollapse(){
      let item = localStorage.getItem("lastCollapse");
      if (item) {
        let generated = "a[name=" + item + "]";
        document.getElementById(item).classList.toggle("show")
        document.querySelector(generated).setAttribute("aria-expanded", "true")
      } else {
        let elements = document
            .getElementById("body")
            .getElementsByClassName("collapse");
        elements[0].classList.toggle("show");

        document
            .getElementById("body")
            .getElementsByClassName("nav-link")[0]
            .setAttribute("aria-expanded", "true")
      }
      this.showPage();
    },
    showPage() {
      document.getElementById("loader").style.display = "none";
      document.getElementById("app").style.display = "block";
    },
    hardRefresh(link) {
      this.lang=link;
      if (this.lang == 'az') {
        this.readMoreText = "daha çox"
        this.readLessText = 'daha az'
        this.menuText = 'Menyu'
        this.mainCategory = categories.categoriesAZ;
        this.homePageText="Ana Səhifə"
        this.contactText="Əlaqə"
      } else if (this.lang == 'en') {
        this.readMoreText = 'read more'
        this.readLessText = 'read less'
        this.menuText = "Menu"
        this.homePageText="Home Page"
        this.contactText="Contact"
        this.mainCategory = categories.categoriesEN
      } else {
        this.readMoreText = 'читать больше'
        this.readLessText = 'читать меньше'
        this.menuText = 'Меню'
        this.homePageText="Главная"
        this.contactText="Связаться с нами"
        this.mainCategory = categories.categoriesRU;
      }
    },
  },

  data() {
    return {
      lang: "az",
      mainCategory: {},
      readMoreText: "",
      readLessText: "",
      menuText: "",
      homePageText: "",
      contactText: ""

    };
  },
  mounted() {
    window.addEventListener("load", () => {
      this.prepareCollapse();
    });

  },
};
</script>
<style>
</style>
